.promo {
    padding: 110px 0 70px;
    text-align: left;

    .promo__title {
        font-style: normal;
        font-weight: 400;
        font-size: 22px;
        line-height: 27px;
        letter-spacing: -0.04em;
        color: #fff;
        padding-bottom: 23px;
        border-bottom: 1px solid #DADADA;
    }
}

@media screen and (max-width: 900px) {
    .promo {
        padding: 90px 0 66px;
    }
}

@media screen and (max-width: 519px) {
    .promo {
        padding: 70px 0 60px;

        .promo__title {
            padding-bottom: 28px;
            font-size: 18px;
            line-height: 22px;
        }
    }
}